@import '../../../sass/variables';

@keyframes progress {
  to {
   background-position-x: 30px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  margin-bottom: 20px;

  h2 {
    font-family: $headerFont;
    font-weight: 400;
    margin-bottom: 20px;
    margin-right: 20px;
  }

  .progress {
    width: 300px;
    height: 30px;
    background-color: #6ed1ff;
    border-radius: 12px;
    background-image: linear-gradient(
                    -45deg,
                    #ffffff44 0%,
                    #ffffff44 25%,
                    transparent 25%,
                    transparent 50%,
                    #ffffff44 50%,
                    #ffffff44 75%,
                    transparent 75%,
                    transparent 100%,
    );
    background-size: 30px;
    animation: progress 1s linear infinite;
  }
}

